.popup-activate
  width: 780px
  position: relative
  text-align: center
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.3)
  box-sizing: border-box

  &__head
    position: relative
    display: flex
    justify-content: center
    align-items: center
    height: 84px
    line-height: 84px

  &__wrap
    display: flex
    flex-direction: row
    width: 100%

  &__left
    width: 70%

  &__right
    width: 30%

  &__btn-close
    display: inline-block
    position: absolute
    top: 50%
    right: 0
    transform: translate(-50%)
    width: 15px
    height: 15px
    cursor: pointer

    &::after,
    &::before
      position: absolute
      content: ''
      top: 5px
      right: 0
      width: 15px
      height: 2px
      background-color: #C7C6C8

    &::after
      transform: rotate(45deg)

    &::before
      transform: rotate(-45deg)

  &__title
    font-size: 20px
    opacity: .95
    +SFProDisplaySemibold

  &__content
    padding: 30px 30px 80px
    background-color: #F4F4F5
    border-top: 1px solid $borderGrey
    border-bottom: 1px solid $borderGrey

  &__footer
    padding: 20px 30px

  &__action
    padding-bottom: 20px
    border-bottom: 1px solid $borderGrey

  &__guarantee
    padding-top: 20px
    display: flex
    align-items: center
    justify-content: space-between

  .label
    margin-left: 7px

  &.hide
    display: none

  &.rating
    width: 880px

    .popup-activate

      &__left
        width: 75%
        border-right: 1px solid $borderGrey

      &__right
        width: 25%

      &__action

        button
          width: 300px

    .plan
      width: 180px

      .message
        width: 300px
        left: 50%
        transform: translate(-50%,100%)

      .general
        width: 200px

